.search__history {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
}

.search__history-list {
  max-width: 180px;
  justify-content: flex-start;
  margin: 0 1rem;
  display: flex;
  overflow: hidden;
}

.search__history-list-item {
  max-width: 170px;
  color: #fff;
  max-height: 38px;
  background-color: rgba(16, 33, 54, .5);
  border: 1px solid #1a1a1a;
  border-radius: 50px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  padding: 5px 10px 5px 12px;
  font-size: 14px;
  line-height: 1.214;
  display: flex;
}

.search__city-name, .modal__close-city {
  color: #fff;
  cursor: pointer;
  font-family: Lato;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.search__city-name:hover, .search__city-name:focus, .modal__close-city:hover, .modal__close-city:focus {
  opacity: 1;
  text-decoration: none;
  transition: background-color, color, box-shadow .25s;
}

.search__city-name {
  margin-right: 10px;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.modal__close-city {
  opacity: .5;
}

.hidden-btn {
  visibility: hidden;
}

.prev, .next {
  width: 25px;
  height: 25px;
  cursor: pointer;
  opacity: .54;
  background-color: rgba(0, 0, 0, 0);
  background-image: url("chart.ce961624.svg");
  background-position: center;
  background-repeat: no-repeat;
  border: 1.5px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .7s;
  display: flex;
  top: 122px;
}

.prev:focus, .prev:hover, .next:focus, .next:hover {
  color: #000;
  opacity: 1;
  background-color: rgba(255, 255, 255, .3);
  transition: background-color, color, box-shadow .25s;
}

.prev {
  left: 60px;
  transform: rotate(90deg);
}

.next {
  right: 60px;
  transform: rotate(-90deg);
}

